.sidebar {
  position: fixed;
  top: 0;
  left: -300px; /* oculto por defecto */
  width: 250px;
  height: 100%;
  background: #333;
  color: #fff;
  padding: 20px;
  transition: left 0.3s ease;
  z-index: 1000;
}

.sidebar.open {
  left: 0; /* aparece */
}

.sidebar .close-btn {
  font-size: 24px;
  background: none;
  border: none;
  color: #fff;
  cursor: pointer;
  margin-bottom: 20px;
}

.sidebar ul {
  list-style: none;
  padding: 0;
}

.sidebar li {
  margin: 12px 0;
}

.sidebar a {
  color: #fff;
  text-decoration: none;
  font-size: 16px;
}