.bundles-container {
  padding: 20px;
}

.bundle-form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 20px;
}

.bundle-form input {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 6px;
}

.products-selector {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.products-selector button {
  flex: 1 1 45%;
  padding: 8px;
  background: #eee;
  border: 1px solid #ccc;
  border-radius: 6px;
  cursor: pointer;
}

.bundle-item {
  display: flex;
  justify-content: space-between;
  background: #fafafa;
  padding: 8px;
  margin-bottom: 6px;
  border-radius: 6px;
}

.remove-btn {
  background: #dc3545;
  color: #fff;
  border: none;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  cursor: pointer;
  font-size: 16px;
  line-height: 1;
}

.bundles-table {
  width: 100%;
  border-collapse: collapse;
}

.bundles-table th,
.bundles-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: center;
}

.delete-btn {
  background: #dc3545;
  color: #fff;
  border: none;
  padding: 6px 12px;
  border-radius: 6px;
  cursor: pointer;
  margin-left: 6px;
}