.stats-container {
  padding: 20px;
}

.stats-summary {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
}

.stats-card {
  flex: 1;
  background: #f5f5f5;
  padding: 16px;
  border-radius: 8px;
  text-align: center;
}

.stats-card h3 {
  margin-bottom: 8px;
}

.stats-section {
  margin-bottom: 20px;
}

.stats-section h3 {
  margin-bottom: 8px;
}

.stats-section ul {
  list-style: none;
  padding: 0;
}

.stats-section li {
  background: #fafafa;
  padding: 8px;
  margin-bottom: 6px;
  border-radius: 6px;
}